body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.examples {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    max-width: 1000px;
    margin: 10px auto;
}

.css-animation-example {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.css-animation-example__box {
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100px;
    height: 100px;
    background: tomato;
    -webkit-animation: animation-transform infinite 6s linear;
            animation: animation-transform infinite 6s linear;
    font-size: 50px;
}

.css-animation-example__box--position {
    position: relative;
    background: yellowgreen;
    -webkit-animation-name: animation-left;
            animation-name: animation-left;

}

strong {
    color: red;
}

@-webkit-keyframes animation-transform {
    0% {
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }

    25% {
        -webkit-transform: translateX(-100px);
                transform: translateX(-100px);
    }

    50% {
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }

    75% {
        -webkit-transform: translateX(100px);
                transform: translateX(100px);
    }

    100% {
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

@keyframes animation-transform {
    0% {
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }

    25% {
        -webkit-transform: translateX(-100px);
                transform: translateX(-100px);
    }

    50% {
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }

    75% {
        -webkit-transform: translateX(100px);
                transform: translateX(100px);
    }

    100% {
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

@-webkit-keyframes animation-left {
    0% {
        left: 0;
    }

    25% {
        left: -100px;
    }

    50% {
        left: 0;
    }

    75% {
        left: 100px;
    }

    100% {
        left: 0;
    }
}

@keyframes animation-left {
    0% {
        left: 0;
    }

    25% {
        left: -100px;
    }

    50% {
        left: 0;
    }

    75% {
        left: 100px;
    }

    100% {
        left: 0;
    }
}

.js-animation-example__box {
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100px;
    height: 100px;
    background: cornflowerblue;
    font-size: 50px;
    margin: 30px;
    will-change: transform;
}



.main {
  text-align: center;
}

